<template>
  <div class="settings-page">
    <h1>알림 설정</h1>
    <ul class="notification-list">
      <li class="notification-item">
        <span>새 메시지 알림</span>
        <ToggleSwitch v-model="settings.newMessageAlert"/>
        <!-- v-model: 컴포넌트의 value와 텍스트, 필드, 토글스위치 등을 연결하여
        사용자가 UI값 변경 시 해당값이 데이터에 반영됨 -->
      </li>
      <li class="notification-item">
        <span>메시지 알림</span>
        <ToggleSwitch v-model="settings.messageAlert"/>
      </li>
      <li class="notification-item">
        <span>소리</span>
        <ActiveToggleSwitch v-model="settings.sound"/>
      </li>
      <li class="notification-item">
        <span>진동</span>
        <ActiveToggleSwitch v-model="settings.vibration"/>
      </li>
      <li class="notification-item">
        <span>진동 패턴</span>
        <ActiveToggleSwitch v-model="settings.vibrationPattern"/>
      </li>
      <li class="notification-item">
        <span>알림 표시</span>
        <ToggleSwitch v-model="settings.notificationDisplay"/>
      </li>
      <li class="notification-item">
        <span>키워드 알림</span>
        <ToggleSwitch v-model="settings.keywordAlert"/>
      </li>
      <li class="notification-item">
        <span>답장 메시지 알림</span>
        <ToggleSwitch v-model="settings.replyMessageAlert"/>
      </li>
      <li class="notification-item">
        <span>보이스룸 알림</span>
        <ActiveToggleSwitch v-model="settings.voiceRoomAlert"/>
      </li>
    </ul>
    <!-- 저장 버튼 -->
    <div class="container">
      <button class="save-button" @click="saveSettings">
        저장
      </button>
    </div>
    <!-- 저장 팝업 -->
    <!-- v-if와 @click.stop
    Vue.js에서 조건부 렌더링과 이벤트 처리에 사용되는 디렉티브 
    조건부 렌더링을 처리하는 Vue.js의 디렉티브입니다. 
    특정 조건이 true일 때만 해당 HTML 요소를 렌더링 -->
    <div class="popup-overlay" v-if="showPopup" @click="closePopup">
      <div class="popup" @click.stop>
        <p>설정이 저장되었습니다.</p>
        <button class="popup-close-button" @click="closePopup">확인</button>
      </div>
    </div>
  </div>
</template>

<script>
  import ToggleSwitch from './components/ToggleSwitch.vue';
  import ActiveToggleSwitch from './components/ActiveToggleSwitch.vue';
  export default {
    components: {
      ToggleSwitch,
      ActiveToggleSwitch
    },
    data() {
      return {
        settings : {
          newMessageAlert: false, // 새 메시지 알림 상태 (기본값: false)
          messageAlert: false, // 일반 메시지 알림 상태 (기본값: false)
          sound: true, // 소리 알림 설정 (기본값: true)
          vibration: true, // 진동 알림 설정 (기본값: true)
          vibrationPattern: false, // 진동 패턴 설정 (기본값: false)
          notificationDisplay: false, // 알림 표시 여부 (기본값: false)
          keywordAlert: false, // 키워드 알림 설정 (기본값: false)
          replyMessageAlert: false, // 답장 메시지 알림 설정 (기본값: false)
          voiceRoomAlert: true, // 보이스룸 알림 설정 (기본값: true)
        },
        showPopup: false, // 팝업을 숨김 상태로 시작
      };
    },
    methods: {
      // saveSettings 메서드는 설정을 저장하고, 팝업을 표시하는 역할을 합니다.
      saveSettings() {
        console.log("저장된 설정:", this.settings); // 콘솔에 현재 설정값 출력
        this.showPopup = true; // 팝업을 표시하려면 true로 설정
      },
      
      // closePopup 메서드는 팝업을 닫는 역할을 합니다.
      closePopup() {
        this.showPopup = false; // 팝업을 숨기려면 false로 설정
      }
    }
  };
</script>

<style scoped>
.settings-page {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.notification-list {
  list-style-type: none;
  padding: 0;
}

.notification-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.save-button {
  padding: 15px 40px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.save-button:hover {
  background-color: #0E7C12;
}

.save-button:active {
  background-color: #3A3A3A;
}

/* 팝업 오버레이 */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* 검정 배경, 투명도 70% */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* 다른 콘텐츠 위에 표시 */
  cursor: pointer;
}

/* 팝업 박스 */
.popup {
  background-color: white;
  padding: 20px 40px;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  color: #333;
}

.popup p {
  margin: 0;
  margin-bottom: 20px;
}

/* 팝업 확인 버튼 */
.popup-close-button {
  padding: 10px 40px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.popup-close-button:hover {
  background-color: #0E7C12;
}

.popup-close-button:active {
  background-color: #3A3A3A;
}
</style>