<template>
  <div class="switch-container">
    <div class="switch" :class="{ 'active': isActive }" @click="toggle">
      <div class="toggle"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: true, // 기본 활성화 상태로 설정
    },
  },
  data() {
    return {
      isActive: this.value, // 컴포넌트 초기화 시 기본값 적용
    };
  },
  watch: {
    value(newValue) {
      this.isActive = newValue; // 부모로부터의 상태 변경 동기화
    },
  },
  methods: {
    toggle() {
      this.isActive = !this.isActive; // 상태 변경
      this.$emit('input', this.isActive); // 상태를 부모에게 전달
    },
  },
};
</script>

<style scoped>
.switch-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.switch {
  width: 60px;
  height: 26px;
  background-color: #d3d3d3; /* 비활성 상태 색상 */
  border-radius: 20px;
  position: relative;
  cursor: pointer;
  transition: background-color 0.3s;
}

.switch.active {
  background-color: #4caf50; /* 활성 상태 색상: 초록색 */
}

.toggle {
  width: 20px;
  height: 20px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 3px;
  left: 3px;
  transition: left 0.3s;
}

.switch.active .toggle {
  left: 36px; /* 활성화된 상태에서 토글의 위치 */
}
</style>
